import { useTranslations } from "next-intl"

export default function PricingPage() {
  const t = useTranslations("PricingPage")

  const plans = [
    {
      name: "Basic",
      price: 99,
      features: ["Feature 1", "Feature 2", "Feature 3"],
    },
    {
      name: "Premium",
      price: 199,
      features: ["Feature 1", "Feature 2", "Feature 3", "Feature 4", "Feature 5"],
    },
  ]

  return (
    <div className="container mx-auto px-4 py-16">
      <h1 className="text-4xl font-bold mb-8">{t("pricing")}</h1>
      {plans.map((plan) => (
        <div key={plan.name} className="bg-white rounded-lg shadow-md p-6 mb-8">
          <h2 className="text-2xl font-bold mb-4">{plan.name}</h2>
          <div className="flex items-baseline mb-2">
            <span className="text-4xl font-bold text-primary-900">${plan.price}</span>
            <span className="text-primary-600 ml-2">/4 months</span>
          </div>
          <p className="text-xs text-primary-600">
            Pay in full or in installments (installment plan increases total cost)
          </p>
          <p className="text-sm text-primary-600 mt-1">{t("paymentOptions")}</p>
          <ul className="list-disc list-inside mt-4">
            {plan.features.map((feature) => (
              <li key={feature}>{feature}</li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  )
}

export const metadata = {
  title: "Pricing",
  locales: ["en", "es"],
}

